@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  /* font-family: "Montserrat", sans-serif; */
  font-family: "Poppins", sans-serif;
  font-weight: 100;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

#mainbox {
  /* background: linear-gradient(#3fbfe6, #80ffdb, #64dfdf, #48bfe3, #5390d9); */
  /* background: url(https://img.freepik.com/premium-photo/abstract-solid-blue-gradient-background_677677-1.jpg); */
  background-color: #05e8ba;
  background-image: linear-gradient(315deg, #05e8ba 0%, #087ee1 74%);

  width: 75dvw;
  height: 90dvh;
  border: solid white;
  border-radius: 25px;
  color: white;
}
.upperbox {
  border: none;
  height: 30dvh;
  width: 75dvw;
  margin-top: 15vb;
}

#dateplacebox {
  border: none;
  float: left;
  /* display: flex; */
  /* flex-direction: column-reverse; */
  min-height: fit-content;
  width: fit-content;
  margin-left: 20dvh;
  margin-top: 8%;
}
#date {
  font-size: 25px;
}
#place {
  text-transform: capitalize;
}

#iconbox {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-right: rgba(255, 255, 255, 0.384) 2px dashed;
  float: left;
  min-height: fit-content;
  width: 30dvw;
  margin-top: 0vb;
  margin-left: 5%;
  margin-right: 8%;
  /* margin-bottom: 10%; */
}
#weathertext {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10%;
  position: absolute;
  /* margin-right: 0vb; */
}

#degreebox {
  border: none;
  height: fit-content;
  width: fit-content;
  margin-right: 15vb;
  margin-top: 7%;
  float: left;
  /* display: flex;
  flex-direction: column; */
}
#realdegree {
  font-size: 30px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  text-align: left;
}
.lowerbox {
  border: none;
  height: 35dvh;
  width: 80dvw;
  margin-top: 5vb;
}
#whitecard {
  border: solid rgb(255, 255, 255) 2px;
  background: whitesmoke;
  color: #002652;
  width: 60.4dvw;
  height: 29dvh;
  margin-top: 2vb;
  margin-left: 16dvh;
  /* margin-right: 15vb; */
  border-radius: 25px;
}
#cardtopic {
  min-height: fit-content;
  width: 60dvw;
  border: none;
  margin-top: 1vb;
  margin-left: 5vb;
  margin-bottom: 10px;
}
.sevenicons {
  width: 70dvw;
  height: 30dvh;
  margin-left: 0vb;
  border: none;
}

.oneicon {
  width: 8.5dvw;
  height: 18dvh;
  margin-top: 1vb;
  border: none;
  border-right: rgb(230, 227, 227) 2px dotted;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  float: left;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 50%;
  border: 0px solid #000000;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 33px;
  height: 31px;
  border: 1px solid #00b4cc;
  background: #01e1ff;
  text-align: left;
  color: #ffffff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 10px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 50%;
  position: absolute;
  top: 14%;
  left: 60%;
  transform: translate(-50%, -50%);
}
/* WEATHER ICONS */
.sunny-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #fbd042;
  position: relative;
  animation: sunny-animation 2s linear infinite;
}

@keyframes sunny-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.sunny-icon::before,
.sunny-icon::after {
  content: "";
  position: absolute;
}

.sunny-icon::before {
  top: 10px;
  left: 10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fbd042;
  animation: sunny-ray-animation 2s linear infinite;
}

@keyframes sunny-ray-animation {
  0% {
    transform: scale(1);
    opacity: 0;
  }
  50% {
    transform: scale(1.3);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

.sunny-icon::after {
  top: 50px;
  left: 50px;
  width: 5px;
  height: 40px;
  background-color: #fbd042;
  transform-origin: bottom center;
  animation: sunny-beam-animation 2s linear infinite;
}

@keyframes sunny-beam-animation {
  0% {
    transform: scaleY(1);
    opacity: 0;
  }
  50% {
    transform: scaleY(1.5);
    opacity: 1;
  }
  100% {
    transform: scaleY(1);
    opacity: 0;
  }
}
.smallsunny-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fbd042;
  position: relative;
  animation: sunny-animation 2s linear infinite;
  margin-left: 5vb;
}

